.nav-bar{

    font-size: 20px;
    font-family: arial;
    background-color:#9CD3D9;
    height: 40px;
  }
  
  .nav-bar ul li a{
    text-decoration: none;
    color: black;
    padding: 20px;
  
  }
  
  .nav-bar ul li{
    display: inline-block;
    -webkit-transition: border 1s, border-radius 1s, padding 1s, background-color 1s, color 1s;
  }
  
  .nav-bar ul li:hover{
    border-bottom: 2px solid gray;
    border-radius: 10%;
    padding: 10px;
    background-color: #F2D5CE;
    color: black;
  }
  